___CSS_LOADER_EXPORT___.push([module.id, `.sidebar-container {
  height: 100vh;
  position: fixed;
  top: 80px;
  z-index: 100000000;
  width: 16%;
  /* border: solid 2px black; */
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
}

.sidebar-item {
  display: flex;
  align-items: center;
  padding: 15px 20px;
  text-decoration: none;
  color: #333;
  font-weight: 500;
  transition: background-color 0.2s;
}

.sidebar-item:hover {
  background-color: #f1f1f1;
}

.sidebar-item.active {
  background-color: #e0e0e0;
  font-weight: bold;
}

.sidebar-icon {
  margin-right: 15px;
  font-size: 20px;
}

.sidebar-text {
  font-size: 16px;
}

/* Optional: Styling for responsiveness */
@media (max-width: 768px) {
  .sidebar-container {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar-nav {
    flex-direction: row;
    overflow-x: auto;
    white-space: nowrap;
  }
  .sidebar-item {
    padding: 10px 15px;
    flex: 1;
    justify-content: center;
  }
  .sidebar-text {
    display: none;
  }
  .sidebar-icon {
    font-size: 24px;
  }
}
